import { NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import { getUserFromCookie } from "@/lib/auth";
import formidable from "formidable-serverless";
import fs from "fs";
import axios from "axios";

export const config = {
  api: {
    bodyParser: false, // Important for file uploads
  },
};

export async function POST(req) {
  const user = await getUserFromCookie(req);
  if (!user) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

  const form = new formidable.IncomingForm();
  form.keepExtensions = true;

  const data = await new Promise((resolve, reject) => {
    form.parse(req, (err, fields, files) => {
      if (err) reject(err);
      else resolve({ fields, files });
    });
  });

  const { title, content } = data.fields;
  let image = null;
  let video = null;

  // Upload image to Cloudinary if present
  if (data.files.image) {
    const imageData = fs.readFileSync(data.files.image.filepath);
    const formData = new FormData();
    formData.append("file", imageData);
    formData.append("upload_preset", process.env.CLOUDINARY_UPLOAD_PRESET);
    
    const res = await axios.post(
      `https://api.cloudinary.com/v1_1/${process.env.CLOUDINARY_CLOUD_NAME}/image/upload`,
      formData,
      { headers: formData.getHeaders() }
    );
    image = res.data.secure_url;
  }

  // Upload video to Cloudinary if present
  if (data.files.video) {
    const videoData = fs.readFileSync(data.files.video.filepath);
    const formData = new FormData();
    formData.append("file", videoData);
    formData.append("upload_preset", process.env.CLOUDINARY_UPLOAD_PRESET);
    
    const res = await axios.post(
      `https://api.cloudinary.com/v1_1/${process.env.CLOUDINARY_CLOUD_NAME}/video/upload`,
      formData,
      { headers: formData.getHeaders() }
    );
    video = res.data.secure_url;
  }

  const post = await prisma.post.create({
    data: {
      title,
      content,
      image,
      video,
      authorId: user.id,
    },
  });

  return NextResponse.json({ post });
}
